@import '../variables';

.sort {
  position: relative;
  cursor: pointer;
  white-space: nowrap;
  &.active {
	.sort__popup {
	opacity: 1;
	visibility: visible;
	}
	.sort__label {
		svg {
			transform: rotate(0deg);
		}
	}
  }
  &__label {
    display: flex;
    align-items: center;

    svg {
      margin-right: 8px;
		transform: rotate(-180deg);
		transition: all 0.3s ease 0s;
    }

    b {
      margin-right: 8px;
    }

    span {
      color: $orange;
      border-bottom: 1px dashed $orange;
      cursor: pointer;
    }
  }

  &__popup {
    position: absolute;
    right: 0;
    margin-top: 15px;
    background: #ffffff;
    box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.09);
    border-radius: 10px;
    overflow: hidden;
    padding: 10px 0;
	 opacity: 0;
	 visibility: hidden;
	 transition: all 0.3s ease 0s;
    ul {
      overflow: hidden;
      li {
        padding: 12px 20px;
        cursor: pointer;

        &.active,
        &:hover {
          background: rgba(254, 95, 30, 0.05);
        }

        &.active {
          font-weight: bold;
          color: $orange;
        }
      }
    }
  }
}
